import { configureStore, createSlice } from "@reduxjs/toolkit";

const cart = createSlice({
  name: 'cart',
  initialState: [], //     ,      id   
  reducers: {
    addItem(state, action) { //addItem    state        
      //state.push(action.payload) //   ->    ()      
      const index = state.findIndex((findId) => { return findId.id === action.payload.id})
      if(index > -1) {
        state[index].count++
      } else {
          state.push(action.payload)
      }
    }, //addItem ,     
    deleteItem(state, action){
      const index = state.findIndex((findId) => { return findId.id === action.payload})
      //            id  
      state.splice(index, 1)
    }, //deleteItem
    addCount(state, action){
      const index = state.findIndex((findId) => {return findId.id === action.payload})
        state[index].count++
    }, //addCount
    subCount(state, action){
      const index = state.findIndex((findId) => {return findId.id === action.payload})
      if(state[index].count > 1) state[index].count--
    } //subCount
  }
})

//addItem  action.payload   id title count 3   id  findId    id   id 
//deleteItem   id action.payload  ,   id   id   

//setter    addItem    reducers action , dispatch 
export const { addItem, deleteItem, addCount, subCount } = cart.actions

//    (import)  state 
export default configureStore({
  reducer: {
    cart: cart.reducer //cart state   -> useSelector       useSelector 
  }
})