import React from "react";
import "./Style.css";
import styled from 'styled-components';

import { useState } from 'react';

const TabMenu = styled.ul`
  color: #c3cac8;
  font-weight: bold;
  font-size: 16px;
  display: flex;
  flex-direction: row;
  align-items: center;
  list-style: none;
  margin: 10px 0;
  cursor: pointer;

  .submenu {
    width: 50%;
    padding: 10px;
    font-size: 15px;
    transition: 0.5s;
  }

  .focused {
    color: #f3f0eb;
  }
`;

const Line = styled.div`
  width: 80%;
  height: 1px;
  background-color: #fff;
  margin: 0 auto;
`;

const About = styled.div`
  text-align: center;
  font-size: 15px;

  & p.address {
    margin: 40px 0 5px;
  }

  & p.time {
    margin-bottom: 30px;
  }
`;


export default function Tab () {
  // Tab Menu    Tab     currentTab  currentTab    ,  0.
  const [currentTab, clickTab] = useState(0);

  const houseArr = [
    { name: 'HOUSE OF AROMATICA ', 
      address: '   162 41-4',
      time: ' 11:00 - 19:00',
      image: process.env.PUBLIC_URL+'/images/house_01.jpg',
    },
    { name: ' ', 
      address: '   750   1',
      time: ' 11:00 - 19:00',
      image: process.env.PUBLIC_URL+'/images/hanam_01.jpg',
    }
  ];

  const selectMenuHandler = (index) => {
    // parameter      ,  (event)  
    //      Tab Menu  .
    clickTab(index);
  };

  return (
    <>
      <div className="kor">
        <TabMenu>
         {houseArr.map((el,index) => (
             <li className={index === currentTab ? "submenu focused" : "submenu" }
             onClick={() => selectMenuHandler(index)}>{el.name}</li>
          ))}
        </TabMenu>
        <Line/>
        <About>
          <p className="address">{houseArr[currentTab].address}</p>
          <p className="time">{houseArr[currentTab].time}</p>
          <img className="storeImage" src={houseArr[currentTab].image} alt="storeImage" style={{width: "80%", margin: "0 auto 60px"}}></img>
        </About>
      </div>
    </>
  );
};